$color-primary: #1F2D3D;
$color-text:  #3C4858;
$color-subtext: #8492A6;
$color-link-internal: #1FB6FF;
$color-link-external: #FF49DB;
$color-snow: #E0E6ED;
$color-border: $color-snow;
$color-light: #fbfbfb;
$color-box-background: mix($color-primary, white, 4%);
$border-radius: 4px;
$font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";

@media (prefers-color-scheme: dark) {

}

body, html {
  font-size: 20px;
}
  
body {
  font-family: $font-family; 
  background: $color-light;
  box-sizing: content-box; 
  line-height: 1.7; 
  overflow-x: hidden;
  color: $color-text;
  font-size: 1rem; 
  -webkit-font-smoothing: antialiased;
  padding: 30px; 
  counter-reset: sidenote-counter; 
  @media (min-width: 600px) {
    padding: 100px;
  }
  @media (min-width: 1200px) {
    padding: 80px;
    max-width: calc(50% - 80px);
  }
  @media (min-width: 1600px) {
    padding: 100px;
    max-width: 700px;
  }
}

section {
  margin-top: 100px;
}

.break-out {
  position: relative;
  right: 0%;
  left: 0%;
  margin-left: -0vw;
  margin-right: -0vw;
  max-width: 100vw;
  width: 100vw;
}

time {
  display: block;
  color: $color-subtext;
  margin: 0.5em 0 1em;
}

footer {
  margin: 100px 0;
  font-size: 0.8em;
  color: $color-subtext;
  padding-top: 1em;
  a { 
    color: $color-subtext;
    border-bottom: 1px solid $color-subtext; 
    background: 0;
    &:after {
      content: ""
    }
  }
}

.webrings {
  margin-top: 50px;
  a {
    border: 0;
    font-weight: normal !important;
  }
}
.journal {
  article {
    margin-bottom: 200px;
  }
}

header {

  a { 
    color: $color-primary;
    border-bottom: 0; 
    background: 0;
    &:after {
      content: ""
    }
  }
}
 

blockquote {
  padding: 0 1.5em;
  margin: 1rem 0;
  font-size: 0.88em;
  border-left: 5px solid rgba($color-subtext,0.2);

  p {
    margin: 0;
  }
}

hr {
  width: 100%;
  border: 0;
  height: 1px;
  margin: 3em 0;
  background: $color-border;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $color-primary;
  line-height: 1.3;
  margin-bottom: 0;
  padding-bottom: 0;
  font-weight: normal;
}

a {
  transition: background 300ms; 
  text-decoration: none;
  color: $color-primary;
  font-weight: normal; 
  font-weight: 500; 
  background: rgba($color-link-external,0.1);
  border-bottom: 3px solid $color-link-external;
  // &:after {
  //   position: relative;
  //   top: -0.5em;
  //   font-size: 0.7em;
  //   content: "â†—";
  //   color: #aaaaaa;
  // }
  &.internal-link {
    color: $color-primary;
    background: rgba($color-link-internal,0.1);
    border-bottom: 3px solid $color-link-internal;
  }     
  &.internal-link:after,
  &.footnote:after,
  &.reversefootnote:after {
    content: "";
  }
} 


*:focus {
  // background: #ffe8bc !important;
  // color: black !important;
}
 

