.backlink-box {
  background: $color-box-background;
  padding: 1em;
  border-radius: $border-radius;
}

code {
  background: #f5f5f5;
  padding: 0.1em 0.2em;
  border-radius: 4px;
}

.invalid-link {
  color: #444444;
  cursor: help;
  background: #fafafa;
  padding: 0 0.1em;
}

.invalid-link-brackets {
  color: #ccc;
  cursor: help;
}

 
 
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 500;
} 

.note {
  margin: 100px 0;
  header {margin-bottom: 50px;}
  side {
    margin-top: 100px;
  }
}
main {
  margin: 200px 0;
}

.journal {
  article {
    margin-bottom: 200px;
  }
}


article {
  h3 {
    margin-top: 2rem;
  }
}
 

.categories {    
  -webkit-columns: 4;
  -moz-columns: 4;
  columns: 4;
  // -webkit-column-gap: 2em;
  // -moz-column-gap: 2em;
  // column-gap: 2em; 
   
  ul {
    li {
      ul {
        
      }
    }
  }
  // h3 {
  //   margin: 0;
  // }
}


img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}


figure {
  padding: 0;
  margin: 3rem 0;
  position: relative;
  img {
    margin: 0;
  }
  figcaption { 
    color: $color-subtext;
    font-size: 80%;
  }
}

strong, b {
  font-weight: 500;
} 

iframe {
  .sidenote,
  .marginnote {
    display: none;
  }
}


figcaption,
.sidenote,
.marginnote {
  a { 
    color: $color-subtext;
    border-bottom: 1px solid $color-subtext;
    background: 0;
  }
}



.sidenote, .marginnote { 


  @media screen and (min-width: 1200px) {
    position: absolute; 
    right: 0;
    // float: right;
    // clear: right;
    // margin-right: 45%;
    margin-right: 250px;
    width: 250px;
    margin-top: 0;
    margin-bottom: 1.96rem;
  } 
  color: $color-subtext;
  font-size: 80%;
  line-height: 1.96; //changed to bring line heights into rational pattern
  vertical-align: baseline; 
}


