.sidenote-number { 
  counter-increment: sidenote-counter;  
}

.sidenote-number:after, 
.sidenote:before { 
  content: counter(sidenote-counter) " ";
    position: relative;
    vertical-align: baseline; 
  }

.sidenote-number:after { 
  content: counter(sidenote-counter);
  font-size: .6rem;
  top: -0.5rem;
  left: 0.1rem;  

